@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;700&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ul,
ol {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Grocery List items */

body {
  background-color: #fffefd;
  font-family: 'Space Grotesk', Arial, sans-serif;
  font-size: 1rem;
}

button {
  display: inline-block;
  margin: 0.5rem;
  padding: 0.5rem 1.5rem;
  border: 3px dotted #69585f;
  font-family: 'Space Grotesk', Arial, sans-serif;
  text-transform: uppercase;
  background-color: #fff;
  cursor: pointer;
  transition: 0.5s;
}

button:hover {
  background-color: #ffa686;
  font-weight: bold;
  border-color: black;
}

.app-container {
  width: 60vw;
  margin: 0 auto;
  margin-top: 10vh;
  padding-top: 5vh;
  padding-bottom: 5vh;
  background-color: #fcddbc;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.grocery-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

h3 {
  font-size: 2.5rem;
  padding: 0.5rem;
  font-weight: bold;
  letter-spacing: 0.5rem;
  text-transform: capitalize;
  text-align: center;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
}

.input-form-grocery {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.input-grocery {
  -webkit-flex-basis: 40%;
          flex-basis: 40%;
  margin: 0.5rem;
  padding: 0.5rem 1.5rem;
  border: 3px solid #69585f;
  font-family: 'Space Grotesk', Arial, sans-serif;
  font-size: 1rem;
}

.input-grocery:focus {
  margin: 0.5rem;
  padding: 0.5rem 1.5rem;
  border: 3px solid black;
  outline: none;
}

input.input-grocery::-webkit-input-placeholder {
  font-family: 'Space Grotesk', Arial, sans-serif;
  font-size: 1rem;
}

input.input-grocery:-ms-input-placeholder {
  font-family: 'Space Grotesk', Arial, sans-serif;
  font-size: 1rem;
}

input.input-grocery::placeholder {
  font-family: 'Space Grotesk', Arial, sans-serif;
  font-size: 1rem;
}

.submit-btn {
  -webkit-flex-basis: 20%;
          flex-basis: 20%;
}

.alert {
  margin-bottom: 2vh;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  padding: 1rem;
  font-weight: bold;
}

.alert-danger {
  background-color: #ed6a5a;
}

.alert-success {
  background-color: #b8d8ba;
}

.grocery-list-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.grocery-list {
  -webkit-flex-basis: 70%;
          flex-basis: 70%;
  margin: 0.5rem;
}

.grocery-list-item {
  font-weight: bold;
  font-size: 1.2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0.5rem;
  padding: 0.2rem 1.5rem;
}

.grocery-list-item:hover {
  background-color: #ffa686;
}

.grocery-list-item-title {
  -webkit-flex-basis: 80%;
          flex-basis: 80%;
}

.btn-list {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.btn-list button {
  padding: 0.1rem 0rem 0.1rem 0.3rem;
  border: none;
  background-color: transparent;
  font-size: 1.3rem;
}

.clear-btn {
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
}

@media screen and (max-width: 1024px) {
  .grocery-list {
    -webkit-flex-basis: 80%;
            flex-basis: 80%;
  }

  .submit-btn {
    -webkit-flex-basis: 25%;
            flex-basis: 25%;
  }
}

@media screen and (max-width: 867px) {
  h3 {
    font-size: 1.5rem;
  }

  .app-container {
    width: 80vw;
  }
  .input-form-grocery {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

  .grocery-list-item {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .submit-btn {
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
  }
  .clear-btn {
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
  }
}

@media screen and (max-width: 545px) {
  .grocery-list {
    -webkit-flex-basis: 10%;
            flex-basis: 10%;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

